<template>
  <section id="how-to-play">
		<div class=""> 
			<div class="result-card p-5">
				<div class="d-flex justify-content-between align-items-center border-boda pb-3">
					<h3>Current ballance</h3>
					<div class="text-center">
						<h4 class="ml-5">{{userBalance.currency}}<span>{{userBalance.amount}}</span><br></h4> 
						<h6 class="ml-4">N100,748.94 - N 10,000.48</h6>
					</div>
				</div>
				<div class="mt-4">
					<p class="p2">Account Number</p>
					<input v-model="account_number" type="text" class="form-control p-4" required>
				</div>
				<button @click.prevent="save($event)" class="btn play mt-5 py-2 px-5">Save</button> 
			</div>
		</div>
	</section>
</template>

<script> 
import { mapState, mapMutations, mapActions } from 'vuex';
export default {  
	data(){
		return{
			account_number: ''
		}
	},
	watch:{
		'account_number': function(newValue){
				const result = newValue.replace(/\D/g, "")
						.replace(/\B(?=(\d{3})+(?!\d))/g, "");
				this.$nextTick(() => this.account_number = result);
				
		}
	},
	methods: {
		...mapActions(['updateAccountNumber']),
		async save($event){
			
			$event.target.innerText = "...Wait"
			var res = await this.updateAccountNumber(this.account_number)
			if(res){
				$event.target.innerText = "Save"
			}
		}
	},
	computed:{
		...mapState(['userBalance'])
	}
}
</script>
<style scoped> 
#how-to-play {
	background: #fffffe;
} 
#how-to-play .border-boda {
	border-bottom: 2px solid rgba(20, 0, 5, 0.1)!important;
} 
#how-to-play .p2 {
	font-size: 24px; 
	color: #666666; 
}
#how-to-play .amount {
	margin-bottom:10em; 
}
#how-to-play .max-span{
	font-size: 18px;
}
#how-to-play h6 {
	font-family: Graphik;
	font-size: 20px; 
	color: #666666;
	font-weight:bold;
}
#how-to-play h4 {
	font-size: 30px;
	color: #269A4C;
	font-weight:bold;
} 
#how-to-play h4 span{
	font-size: 58px;
	color: #269A4C; 
}
#how-to-play .play {
  background-color: #269A4C; 
  color:#fff; 
	font-size: 24px;
	text-transform: none;
	font-weight:bold; 
	box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
}
@media screen and (max-width:1040px) { 
#how-to-play .p2 {
	font-size: 18px;
}
#how-to-play .max-span{
	font-size: 14px;
}
#how-to-play h4 {
	font-size: 20px;
} 
#how-to-play h4 span{
	font-size: 38px;
}
#how-to-play .play {
	font-size: 18px;
}
}
</style>
